import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import Map from "../components/GoogleMaps";

import styles from "../styles/AboutPage.module.css";

function AboutPage() {
  return (
    <div>
      <section className={styles.AboutSection}>
        <div>
          <h1 className={styles.AboutHeadline}>Vilka r vi?</h1>
        </div>
        <hr />
      </section>
      <div className="about-main d-flex flex-nowrap">
        <Container fluid>
          <Row className="featurett">
            <Col md={5}>
              <img
                src="https://res.cloudinary.com/draygqe7t/image/upload/v1699965816/media/images/resizedImage_itrdue.png"
                alt=""
                className={styles.AboutImg}
              />
            </Col>
            <Col md={7}>
              <h3>Maria Starhg</h3>
              <p>
                Maria Starhg r dressyrryttare ut i fingerspetsarna. Sedan nio
                rs lder har det varit dressyr fr hela slanten, ven om
                variation r en stor del av upplgget i vardagen. Maria har
                meriter till och med Grand Prix och ridit SM p ponny, junior
                och young rider. Maria r ven legitimerad Fysioterapeut till
                yrket och lgger stor vikt vid ryttarens sits och inverkan.
                Ridning ska vara roligt fr alla, ryttare som hst. Alla r
                alltid vlkomna oavsett kunskap och niv.
              </p>
            </Col>
          </Row>
          <hr />
          <Row className="featurett">
            <Col md={7}>
              <h3>Gsttrnare Heidi Vnttinen</h3>
              <p>
                Gsttrnare Heidi Vnttinen, vars hst Kiera for U bor i
                stallet, kommer frn Finland, men bor i Skne sedan 2015. I det
                "civila" jobbar hon som sprklrare p hgstadiet. Heidi r
                utbildad LA-domare samt D i dressyr sedan 2018 och blev nyligen
                godknd D ven i WE. Hon har deltagit i bl.a. unghstchampionat
                i Finland och Sverige, lag-FM och har tvlat t.o.m. Prix St
                Georges.
              </p>
            </Col>
            <Col md={5}>
              <img
                src="https://res.cloudinary.com/draygqe7t/image/upload/v1711873942/58f75095-5c63-4e56-a882-6a14483dcd3a_qdx4w3.jpg"
                alt=""
                className={styles.AboutImg}
              />
            </Col>
          </Row>
          <hr />
          <Row className="featurett">
            <Col md={5}>
              <img
                src="https://res.cloudinary.com/draygqe7t/image/upload/v1691502265/123444577_184051683261535_824802158311214574_n_yjw2zu.jpg"
                alt=""
                className={styles.AboutImg}
              />
            </Col>
            <Col md={7}>
              <h3>Ridbanan</h3>
              <p>
                Det r en rymlig 20x60m ridbana med stenmjlsunderlag. Hr finns
                ven belysning och en enklare hinderpark. Vl inhgnad med
                trstaket. Husets trdgrd angrnsar till ridbanan p ena sidan
                och de ppna flten p den andra, vilket kan ge ypperliga
                mjligheter fr miljtrning.
              </p>
            </Col>
          </Row>
          <hr />
          <Row className="featurett">
            <Col md={7}>
              <h3>Sommarbete</h3>
              <p>
                Under sommarhalvret, frn maj till oktober blir vr
                frtrollande sommarhage en fristad fr unga hstar i ldrarna 1
                till 3 r. Med naturen som sin lekplats kan upp till 10 lekfulla
                unghstar strva fritt p cirka 6 hektar kuperat naturbete. Det
                r som ett hstparadis dr de kan strcka p benen, skapa nya
                vnner och bara vara sig sjlva. Vi hller noga koll p dessa
                unga stjrnor och besker dem minst en gnger om dagen, oftast
                tv, fr att se till att de r glada och friska. Regn eller
                solsken, vi r dr fr att skerstlla att de har allt de
                behver. Det frnderliga vdret bestmmer hur lnge de fr
                njuta av detta hstparadis, men oavsett om det r en lng sommar
                eller en kortare en s fr de utmrkt grundtrning i vra
                hrliga backar.
                <hr />
                <strong>Inget bete 2024 pga upprustning.</strong>
              </p>
            </Col>
            <Col md={5}>
              <img
                src="https://res.cloudinary.com/draygqe7t/image/upload/c_scale,h_960,q_auto:best/v1691501957/240594785_373990104267691_8073872753669794635_n_xammpg.webp"
                alt=""
                className={styles.AboutImg}
              />
            </Col>
          </Row>
          <hr />
          <Row className="featurett">
            <Col md={5}>
              <img
                src="https://res.cloudinary.com/draygqe7t/image/upload/v1691502351/123466975_184051633261540_2718395644933478477_n_erz6k8.jpg"
                alt=""
                className={styles.AboutImg}
              />
            </Col>
            <Col md={7}>
              <h3>Stallet</h3>
              <p>
                Hos oss finns mjlighet till inackordering p stall. Hr finns
                totalt sex boxar, var av tv fylls av vra egna hstar. Stallet
                r luftigt och rymligt med tv uppstllningsplatser, spolplats,
                sadelkammare, foderkammare och loge. Hr vistas hstarna ute i
                stora hagar frn ca klockan 06 p morgonen till 20 p kvllen s
                ofta som vdret tillter. Under sommaren gr de ute dygnet runt.
                Hr ingr halm, alla fodringar, ut- och inslpp samt mockning
                alla dagar i veckan, ven julafton, mot att man r behjlplig
                med pass vid behov. Det ska vara enkelt att ha hst hos oss!
              </p>
            </Col>
          </Row>
        </Container>
      </div>
      <hr />
      <Map />
    </div>
  );
}

export default AboutPage;
